/* Apple-inspired Professional Portfolio Styles */
@import url('https://fonts.googleapis.com/css2?family=SF+Pro+Display:wght@300;400;500;600;700;800;900&display=swap');

/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'SF Pro Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  line-height: 1.6;
  color: #1d1d1f;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.98) 0%, rgba(248, 250, 252, 0.98) 100%);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  margin: 0;
  padding: 0;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: 
    radial-gradient(circle at 20% 20%, rgba(0, 122, 255, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(126, 91, 239, 0.02) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.03) 0%, transparent 70%);
  z-index: -1;
  animation: liquidGlow 10s ease-in-out infinite alternate;
  pointer-events: none;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  overflow-x: hidden;
  height: 100%;
}

body {
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f5f5f7;
}

::-webkit-scrollbar-thumb {
  background: #d1d1d6;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a1a1a6;
}

/* Liquid Glass animations */
@keyframes liquidFadeIn {
  from { 
    opacity: 0;
    filter: blur(10px);
    transform: scale(0.95);
  }
  to { 
    opacity: 1;
    filter: blur(0px);
    transform: scale(1);
  }
}

@keyframes liquidSlideIn {
  from { 
    opacity: 0;
    filter: blur(8px);
    transform: scale(0.9) rotateX(10deg);
  }
  to { 
    opacity: 1;
    filter: blur(0px);
    transform: scale(1) rotateX(0deg);
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-10px); }
}

@keyframes liquidGlow {
  0%, 100% { 
    box-shadow: 0 8px 32px rgba(0, 122, 255, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  }
  50% { 
    box-shadow: 0 12px 40px rgba(0, 122, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
}

/* Utility classes */
.liquid-fade-in {
  animation: liquidFadeIn 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.liquid-slide-in {
  animation: liquidSlideIn 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.float {
  animation: float 3s ease-in-out infinite;
}

.liquid-glow {
  animation: liquidGlow 2s ease-in-out infinite;
}

/* Apple-style glass effect */
.glass-effect {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Gradient text effect */
.gradient-text {
  background: linear-gradient(135deg, #007aff 0%, #5ac8fa 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Card hover effects */
.card-hover {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-hover:hover {
  transform: translateY(-8px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

/* Button styles with subtle glass effect */
.btn-primary {
  background: linear-gradient(135deg, #007aff 0%, #5ac8fa 100%);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  color: white;
  font-weight: 500;
  padding: 12px 24px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -50%;
  width: 50%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.2) 50%, transparent 100%);
  animation: liquidReflection 3s ease-in-out infinite;
  pointer-events: none;
}

.btn-primary:hover {
  transform: translateY(-2px) scale(1.02);
  background: linear-gradient(135deg, #0056cc 0%, #4aa3d9 100%);
  box-shadow: 0 8px 25px rgba(0, 122, 255, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary {
  background: rgba(0, 122, 255, 0.05);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  border: 2px solid rgba(0, 122, 255, 0.3);
  border-radius: 12px;
  color: #007aff;
  font-weight: 500;
  padding: 10px 22px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.btn-secondary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -50%;
  width: 50%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(0, 122, 255, 0.1) 50%, transparent 100%);
  animation: liquidReflection 3.5s ease-in-out infinite;
  pointer-events: none;
}

.btn-secondary:hover {
  background: rgba(0, 122, 255, 0.08);
  border: 2px solid rgba(0, 122, 255, 0.5);
  transform: translateY(-2px) scale(1.02);
  box-shadow: 0 6px 20px rgba(0, 122, 255, 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

/* Section spacing */
.section-padding {
  padding: 80px 0;
}

/* Typography enhancements */
.title-large {
  font-size: 4rem;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.title-medium {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: -0.01em;
}

.subtitle {
  font-size: 1.25rem;
  font-weight: 500;
  color: #86868b;
  line-height: 1.5;
}

/* Project cards with realistic liquid glass effect */
.project-card {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.7) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.1),
    0 2px 8px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.8),
    inset 0 -1px 0 rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 32px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.project-card::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, rgba(255,255,255,0.4) 0%, transparent 50%, rgba(255,255,255,0.1) 100%);
  border-radius: 20px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}

.project-card::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(135deg, rgba(0, 122, 255, 0.6) 0%, rgba(90, 200, 250, 0.6) 100%);
  transition: all 0.4s ease;
  z-index: 2;
  border-radius: 0 2px 2px 0;
}

.project-card:hover {
  transform: translateY(-12px) scale(1.02);
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.85) 100%);
  border: 1px solid rgba(255, 255, 255, 0.6);
  box-shadow: 
    0 25px 60px rgba(0, 0, 0, 0.15),
    0 15px 35px rgba(0, 0, 0, 0.1),
    0 5px 15px rgba(0, 0, 0, 0.05),
    inset 0 2px 0 rgba(255, 255, 255, 0.9),
    inset 0 -2px 0 rgba(255, 255, 255, 0.3),
    inset 2px 0 4px rgba(255, 255, 255, 0.2),
    inset -2px 0 4px rgba(0, 0, 0, 0.02);
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
}

.project-card:hover::before {
  opacity: 1;
}

.project-card:hover::after {
  width: 6px;
  background: linear-gradient(135deg, #007aff 0%, #5ac8fa 100%);
  box-shadow: 0 0 20px rgba(0, 122, 255, 0.4);
}

/* Tech stack icons with realistic glass effect */
.tech-icon {
  width: 56px;
  height: 56px;
  border-radius: 12px;
  background: linear-gradient(145deg, rgba(0, 122, 255, 0.12) 0%, rgba(0, 122, 255, 0.06) 100%);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(0, 122, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #007aff;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 
    0 4px 16px rgba(0, 122, 255, 0.1),
    0 1px 4px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.tech-icon::before {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  background: linear-gradient(135deg, rgba(255,255,255,0.2) 0%, transparent 50%, rgba(255,255,255,0.1) 100%);
  border-radius: 12px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}

.tech-icon:hover {
  transform: scale(1.08) translateY(-2px);
  background: linear-gradient(145deg, rgba(0, 122, 255, 0.18) 0%, rgba(0, 122, 255, 0.1) 100%);
  border: 1px solid rgba(0, 122, 255, 0.3);
  box-shadow: 
    0 8px 25px rgba(0, 122, 255, 0.25),
    0 4px 12px rgba(0, 122, 255, 0.15),
    0 1px 6px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.4),
    inset 0 -1px 0 rgba(255, 255, 255, 0.1),
    inset 1px 0 2px rgba(255, 255, 255, 0.15),
    inset -1px 0 2px rgba(0, 0, 0, 0.02);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.tech-icon:hover::before {
  opacity: 1;
}

/* Status badges */
.status-live {
  background: #34c759;
  color: white;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 0.875rem;
  font-weight: 500;
}

.status-development {
  background: #ff9500;
  color: white;
  padding: 6px 12px;
  border-radius: 16px;
  font-size: 0.875rem;
  font-weight: 500;
}

/* Form styles */
.form-input {
  background: #f9f9f9;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 16px;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 100%;
}

.form-input:focus {
  outline: none;
  border-color: #007aff;
  box-shadow: 0 0 0 3px rgba(0, 122, 255, 0.1);
}

.form-textarea {
  background: #f9f9f9;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  padding: 16px;
  font-size: 1rem;
  transition: all 0.3s ease;
  width: 100%;
  min-height: 120px;
  resize: vertical;
}

.form-textarea:focus {
  outline: none;
  border-color: #007aff;
  box-shadow: 0 0 0 3px rgba(0, 122, 255, 0.1);
}

/* Navigation styles */
.nav-transparent {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}

.nav-link {
  color: #1d1d1f;
  text-decoration: none;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.nav-link:hover {
  color: #007aff;
  background: rgba(0, 122, 255, 0.04);
}

/* Hero section with Liquid Glass effect */
.hero-background {
  background: linear-gradient(135deg, rgba(245, 247, 250, 0.9) 0%, rgba(195, 207, 226, 0.9) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  position: relative;
  overflow: hidden;
}

.hero-pattern {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(0, 122, 255, 0.1) 0%, transparent 70%),
    radial-gradient(circle at 75% 75%, rgba(126, 91, 239, 0.1) 0%, transparent 70%);
}

.hero-pattern::before {
  content: '';
  position: absolute;
  top: 0;
  left: -25%;
  width: 150%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.08) 50%, transparent 100%);
  animation: liquidReflection 8s ease-in-out infinite;
  pointer-events: none;
}

.hero-pattern::after {
  content: '';
  position: absolute;
  top: -25%;
  right: -25%;
  width: 150%;
  height: 150%;
  background: radial-gradient(circle, rgba(255,255,255,0.03) 0%, transparent 70%);
  animation: liquidGlow 6s ease-in-out infinite alternate;
  pointer-events: none;
}

/* Contact section with realistic liquid glass effect */
.contact-card {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.9) 0%, rgba(255, 255, 255, 0.75) 100%);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 20px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.1),
    0 2px 8px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.8),
    inset 0 -1px 0 rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.4);
  padding: 48px;
  position: relative;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.contact-card::before {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(135deg, rgba(255,255,255,0.3) 0%, transparent 50%, rgba(255,255,255,0.1) 100%);
  border-radius: 20px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}

.contact-card:hover {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.85) 100%);
  border: 1px solid rgba(255, 255, 255, 0.6);
  box-shadow: 
    0 20px 50px rgba(0, 0, 0, 0.15),
    0 10px 25px rgba(0, 0, 0, 0.1),
    0 4px 12px rgba(0, 0, 0, 0.05),
    inset 0 2px 0 rgba(255, 255, 255, 0.9),
    inset 0 -2px 0 rgba(255, 255, 255, 0.3),
    inset 2px 0 4px rgba(255, 255, 255, 0.2),
    inset -2px 0 4px rgba(0, 0, 0, 0.02);
  transform: translateY(-8px) scale(1.01);
  backdrop-filter: blur(30px);
  -webkit-backdrop-filter: blur(30px);
}

.contact-card:hover::before {
  opacity: 1;
}

.contact-icon {
  width: 56px;
  height: 56px;
  border-radius: 12px;
  background: linear-gradient(145deg, rgba(0, 122, 255, 0.12) 0%, rgba(0, 122, 255, 0.06) 100%);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(0, 122, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #007aff;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 
    0 4px 16px rgba(0, 122, 255, 0.1),
    0 1px 4px rgba(0, 0, 0, 0.05),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.contact-icon::before {
  content: '';
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  background: linear-gradient(135deg, rgba(255,255,255,0.2) 0%, transparent 50%, rgba(255,255,255,0.1) 100%);
  border-radius: 12px;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease;
  pointer-events: none;
}

.contact-icon:hover {
  transform: translateY(-3px) scale(1.08);
  background: linear-gradient(145deg, rgba(0, 122, 255, 0.18) 0%, rgba(0, 122, 255, 0.1) 100%);
  border: 1px solid rgba(0, 122, 255, 0.3);
  box-shadow: 
    0 8px 25px rgba(0, 122, 255, 0.25),
    0 4px 12px rgba(0, 122, 255, 0.15),
    0 1px 6px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.4),
    inset 0 -1px 0 rgba(255, 255, 255, 0.1),
    inset 1px 0 2px rgba(255, 255, 255, 0.15),
    inset -1px 0 2px rgba(0, 0, 0, 0.02);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
}

.contact-icon:hover::before {
  opacity: 1;
}

/* Footer */
.footer-dark {
  background: #1d1d1f;
  color: #ffffff;
}

.footer-link {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: all 0.3s ease;
}

.footer-link:hover {
  color: #ffffff;
}

/* Loading animations */
.skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%);
  background-size: 200% 100%;
  animation: loading 1.5s infinite;
}

@keyframes loading {
  0% { background-position: 200% 0; }
  100% { background-position: -200% 0; }
}

/* Liquid Glass Animation Keyframes */
@keyframes liquidReflection {
  0% {
    left: -50%;
    opacity: 0;
  }
  50% {
    left: 50%;
    opacity: 0.3;
  }
  100% {
    left: 100%;
    opacity: 0;
  }
}

@keyframes liquidGlow {
  0% {
    opacity: 0.1;
    transform: scale(1) rotate(0deg);
  }
  50% {
    opacity: 0.3;
    transform: scale(1.1) rotate(180deg);
  }
  100% {
    opacity: 0.1;
    transform: scale(1) rotate(360deg);
  }
}

@keyframes liquidShimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

@keyframes glassReflection {
  0% {
    transform: translateX(-100%) skewX(-15deg);
    opacity: 0;
  }
  50% {
    opacity: 0.6;
  }
  100% {
    transform: translateX(200%) skewX(-15deg);
    opacity: 0;
  }
}

/* Liquid Glass Utility Classes */
.liquid-glass {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}

.liquid-glass::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent 0%, rgba(255,255,255,0.4) 50%, transparent 100%);
  animation: liquidReflection 3s ease-in-out infinite;
  z-index: 1;
}

.liquid-glass:hover {
  background: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 25px 50px rgba(0,0,0,0.1), inset 0 1px 0 rgba(255,255,255,0.2);
  transform: translateY(-2px) scale(1.01);
}

/* Add realistic glass reflection effect for cards */
.project-card .glass-reflection,
.contact-card .glass-reflection {
  content: '';
  position: absolute;
  top: 0;
  left: -50%;
  width: 30%;
  height: 100%;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.6) 30%, 
    rgba(255, 255, 255, 0.8) 50%, 
    rgba(255, 255, 255, 0.6) 70%, 
    transparent 100%);
  transform: translateX(-100%) skewX(-15deg);
  opacity: 0;
  transition: all 0.6s ease;
  pointer-events: none;
  z-index: 3;
}

.project-card:hover .glass-reflection,
.contact-card:hover .glass-reflection {
  animation: glassReflection 1.2s ease-out;
}

/* Enhanced shadow depth for more realism */
.project-card:hover,
.contact-card:hover {
  filter: drop-shadow(0 0 0 rgba(255, 255, 255, 0.2));
}

/* Add subtle inner glow effect */
.project-card:hover::after,
.contact-card:hover::after {
  box-shadow: 
    0 0 20px rgba(0, 122, 255, 0.4),
    inset 0 0 20px rgba(255, 255, 255, 0.1);
}

/* Responsive design */
@media (max-width: 768px) {
  .title-large {
    font-size: 2.5rem;
  }
  
  .title-medium {
    font-size: 2rem;
  }
  
  .section-padding {
    padding: 60px 0;
  }
  
  .project-card {
    padding: 24px;
  }
  
  .contact-card {
    padding: 32px;
  }
  
  .hero-background {
    padding: 60px 0;
  }
}

@media (max-width: 480px) {
  .title-large {
    font-size: 2rem;
  }
  
  .title-medium {
    font-size: 1.5rem;
  }
  
  .section-padding {
    padding: 40px 0;
  }
  
  .project-card {
    padding: 20px;
  }
  
  .contact-card {
    padding: 24px;
  }
}

/* Print styles */
@media print {
  * {
    color: black !important;
    background: white !important;
    box-shadow: none !important;
  }
  
  .no-print {
    display: none !important;
  }
  
  .section-padding {
    padding: 20px 0;
  }
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Focus styles for keyboard navigation */
button:focus-visible,
a:focus-visible {
  outline: 2px solid #007aff;
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .card-hover {
    border: 2px solid #000;
  }
  
  .btn-primary {
    border: 2px solid #000;
  }
}